
#edu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 750px;
  margin: 30px auto;
  box-shadow: 0 0 15px 2px #bbb;
}
.edu-block {
  color: #fff;
  width: 250px;
  height: 250px;
  margin: 0;
  background: #ddd;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.edu-block div {
  margin-left: 24px;
}
.edu-block .upper {
  margin-top: 24px;
}
.edu-block .down {
  margin-bottom: 20px;
}
.edu-block p {
  width: 200px;
  margin: 5px;
  font-size: 20px;
  line-height: 1em;
}
.edu-block .large-label {
  vertical-align: text-top;
  text-indent: -4px;
  font-size: 36px;
  font-weight: 600;
  margin-bottom: 0.3em;
}
.edu-school {
  background: url("./background.svg") no-repeat;
  background-size: cover;
  color: #000;
}
.edu-right {
  text-align: end;
}
.edu-right .content {
  margin-top: 20px;
}
.edu-right .main-award {
  font-size: 24px;
  font-weight: 600;
}


