
.subtitle {
  margin: -60px 10%;
  background: url("red.svg") no-repeat;
  background-size: 100% 100%;
  width: 120px;
  height: 120px;
  border-radius: 16px;
  z-index: 999;
  position: absolute;
}
.subtitle .subtitle-content {
  color: #fff;
  padding-left: 10px;
  padding-top: 56px;
  padding-bottom: 20px;
  width: 100px;
}
.subtitle .subtitle-content p {
  margin: 0 5px;
  font-size: 17px;
}


